* {
  margin: 0;
  padding: 0 auto;
  outline: none;
  word-wrap: break-word;
  box-sizing: border-box;
  font-family: "Segoe UI", Tahoma, Geneva, Verdana, sans-serif;
}

/* Nav bar css */

.toggler_container {
  justify-self: right;
  position: relative;
}

#toggler_btn {
  background: transparent;
  border: none;
  display: none;
  padding: 15px;
}

.btn_line {
  display: block;
  background: white;
  width: 20px;
  height: 3px;
}

.middle_line {
  transform: translateY(4px);
}

.bottom_line {
  transform: translateY(8px);
}

nav {
  width: 100%;
  background-color: #292929;
  display: grid;
  height: 50px;
  grid-template-columns: auto;
  z-index: 100;
  position: fixed;
}

.nav_container {
  width: 100%;
  height: 100%;
  display: grid;
  grid-template-columns: auto auto auto auto auto auto auto;
  justify-content: right;
  align-self: center;
}

.nav_item {
  padding-right: 40px;
}

.nav_link {
  cursor: pointer;
  color: white;
  font-weight: bold;
  outline: none;
  text-decoration: none;
  font-size: 1rem;
}

.nav_link:hover {
  border-bottom: 2px solid white;
}

.dropdown_container {
  display: none;
  position: absolute;
}

.dropdown_container > div {
  padding: 15px 15px;
}

.dropdown_link {
  position: relative;
}

.dropdown_link:hover .dropdown_container {
  display: block;
  background-color: #292929;
}

/* Home page CSS */
.home_header {
  display: flex;
  align-items: center;
  background: url(/Parho-Pakistan-Front-End/static/media/yannis-h-uaPaEM7MiQQ-unsplash.e67ff2be.png) center center
    no-repeat;
  background-size: cover;
  height: 550px;
  width: 100%;
  padding-top: 50px;
  position: relative;
}

.home_header::after {
  content: " ";
  width: 100%;
  height: 100%;
  position: absolute;
  display: block;
  background-color: black;
  top: 0;
  left: 0;
  opacity: 0.5;
  z-index: 0;
}

.home_header_content {
  position: absolute;
  padding: 0px 30px;
  color: white;
  z-index: 1;
}

.home_header_content > h1 {
  font-size: 3.5rem;
}

.home_header_content p:nth-of-type(1) {
  font-weight: bold;
  font-size: 1.3rem;
}

.home_header_content p:nth-of-type(2) {
  color: #c1c1c1;
  font-size: 1rem;
  font-style: italic;
}

.home_header_content p:nth-of-type(3) {
  color: #c1c1c1;
  font-size: 1rem;
  font-style: italic;
}

.home_header_btn {
  /* Permalink - use to edit and share this gradient: https://colorzilla.com/gradient-editor/#f53b00+1,fd882a+75,ff9933+100 */
  background: #f53b00; /* Old browsers */ /* FF3.6-15 */ /* Chrome10-25,Safari5.1-6 */
  background: linear-gradient(
    to right,
    #f53b00 1%,
    #fd882a 75%,
    #ff9933 100%
  ); /* W3C, IE10+, FF16+, Chrome26+, Opera12+, Safari7+ */
  filter: progid:DXImageTransform.Microsoft.gradient( startColorstr='#f53b00', endColorstr='#ff9933',GradientType=1 ); /* IE6-9 */
  border: none;
  margin-top: 50px;
  width: -webkit-fit-content;
  width: -moz-fit-content;
  width: fit-content;
  padding: 20px 30px;
}

.home_header_btn > a {
  color: white;
  text-decoration: none;
  font-weight: bold;
  font-size: 1.2rem;
}

#whyus {
  display: grid;
  grid-template-columns: 50% 50%;
  padding: 100px 50px;
  background-color: white;
  width: 100%;
  height: -webkit-fit-content;
  height: -moz-fit-content;
  height: fit-content;
}

.homepage_vid {
  width: 100%;
  height: 100%;
}

.whyus_content {
  padding-left: 50px;
  text-align: start;
}

.whyus_content > h1 {
  font-size: 3rem;
}

.whyus_content > p {
  padding: 30px 0px 50px;
  font-size: 1.2rem;
  color: #717171;
}

.whyus_btn {
  /* Permalink - use to edit and share this gradient: https://colorzilla.com/gradient-editor/#4d90dd+1,00339b+100 */
  background: #4d90dd; /* Old browsers */ /* FF3.6-15 */ /* Chrome10-25,Safari5.1-6 */
  background: linear-gradient(
    to right,
    #4d90dd 1%,
    #00339b 100%
  ); /* W3C, IE10+, FF16+, Chrome26+, Opera12+, Safari7+ */
  filter: progid:DXImageTransform.Microsoft.gradient( startColorstr='#4d90dd', endColorstr='#00339b',GradientType=1 ); /* IE6-9 */

  width: -webkit-fit-content;

  width: -moz-fit-content;

  width: fit-content;
  padding: 20px 30px;
}

.whyus_btn > a {
  color: white;
  font-weight: bold;
  font-size: 1.2rem;
  text-decoration: none;
}

.homepage_details {
  background-color: white;
  display: grid;
  grid-template-columns: 50% auto;
  padding: 50px 0px 0px;
}

.details_img_container > img {
  width: 100%;
  height: 100%;
}

.details {
  font-size: 1rem;
  color: #9a9a9a;
  background-color: #1e1e1e;
  padding: 50px 30px 50px 20px;
  text-justify: auto;
}

.details2 {
  font-size: 1rem;
  color: #9a9a9a;
  background-color: #141414;
  padding: 50px 30px 30px 20px;
  text-justify: auto;
}

.details_content {
  display: grid;
  grid-template-columns: auto auto;
}

.details_img {
  width: 120px;
}

#successstory {
  padding: 50px;
  position: relative;
}

#successstory::after {
  content: " ";
  position: absolute;
  width: 100%;
  height: 100%;
  background-color: grey;
  top: 0;
  left: 0;
  z-index: -1;
  opacity: 0.1;
}

.success_header {
  color: #00508a;
  font-size: 3vw;
  text-transform: uppercase;
  text-align: center;
  letter-spacing: 1px;
}

.success_carousel {
  display: grid;
  grid-template-columns: auto auto auto;
  padding-top: 50px;
  padding-bottom: 280px;
  background-image: url(/Parho-Pakistan-Front-End/static/media/pakmap.6e2c9de1.png);
  background-position: center top;
  background-size: 60%;
  height: 100%;
  background-repeat: no-repeat;
}

.carousel_icon {
  align-self: center;
  font-size: 5vw;
  font-weight: 500;
  color: grey;
  padding: 0px 20px;
  cursor: pointer;
}

.carousel_content {
  display: flex;
  flex-direction: column;
  justify-self: center;
  align-self: center;
}

.carousel_content > img {
  width: 15vw;
  height: 15vw;
  align-self: center;
}

.carousel_content > h3 {
  text-align: center;
  align-self: center;
  padding: 5px;
}

.carousel_content > p {
  align-self: center;
  color: #676767;
  text-justify: auto;
  text-align: start;
  padding: 10px;
}

#sponsorship_sec {
  display: grid;
  grid-template-columns: auto;
  padding: 50px;
  justify-content: center;
}

#sponsorship_sec > h1 {
  font-size: 5vw;
  letter-spacing: 1.5px;
  text-transform: uppercase;
  color: #00339b;
}

.sponsorships {
  display: grid;
  padding: 30px 0;
  grid-template-columns: auto auto;
  grid-column-gap: 50px;
  -webkit-column-gap: 50px;
          column-gap: 50px;
}

.sponsorship_card {
  width: -webkit-max-content;
  width: -moz-max-content;
  width: max-content;
  box-shadow: 0 0 10px rgba(0, 0, 0, 0.2);
  justify-self: center;
}

.card_header {
