  padding: 0px;
  justify-self: center;
}

.card_content > ul > li {
  padding: 5px 0;
  justify-self: center;
  text-align: center;
}

.donation_price {
  font-size: 1.5rem;
  color: #ff7b51;
  font-weight: bold;
}

.content_header {
  font-size: 1rem;
  color: gray;
}

.sponsor_signup_btn {
  /* Permalink - use to edit and share this gradient: https://colorzilla.com/gradient-editor/#f53b00+1,fd882a+75,ff9933+100 */
  background: #f53b00; /* Old browsers */ /* FF3.6-15 */ /* Chrome10-25,Safari5.1-6 */
  background: linear-gradient(
    to right,
    #f53b00 1%,
    #fd882a 75%,
