  padding: 20px 30px;
}

.sponsor_signup_btn > a {
  color: white;
  text-decoration: none;
  font-weight: bold;
  font-size: 1.2rem;
}

#collaborator_sec > h1 {
  text-transform: uppercase;
  font-size: 4vw;
  text-align: center;
  color: #00339b;
}

.collab_img_container {
  padding: 40px 0 100px;
  display: grid;
  grid-template-columns: 1fr 1fr 1fr 1fr;
  justify-items: center;
  align-items: center;
}

.collab_img_container img {
  width: 150px;
}

footer {
  background-color: #292929;
  padding: 50px 20px 100px;
  color: white;
  display: grid;
  grid-template-columns: 1.2fr 1.4fr 0.6fr;
  text-transform: uppercase;
  position: relative;
}

footer > h1 {
  justify-self: start;
  font-size: 4vw;
  border-bottom: 1px solid white;
  padding: 0;
  height: -webkit-fit-content;
  height: -moz-fit-content;
  height: fit-content;
  align-self: flex-start;
}

.website_links {
  padding-top: 60px;
