.login_signup_component {
  background: url(/Parho-Pakistan-Front-End/static/media/navimg.f22b291d.png);
  background-position: center center;
  background-repeat: no-repeat;
  background-size: cover;
  padding: 100px 0;
}

#ls_container {
  display: grid;
  grid-template-columns: auto;
  margin: 0 auto;
  width: -webkit-fit-content;
  width: -moz-fit-content;
  width: fit-content;
  grid-template-rows: auto;
  min-height: 400px;
  border-radius: 20px;
  background-color: rgba(216, 211, 211, 0.5);
}

#tab_btn_container {
  width: -webkit-fit-content;
  width: -moz-fit-content;
  width: fit-content;
  margin: 0 auto;
  height: -webkit-fit-content;
  height: -moz-fit-content;
  height: fit-content;
  text-align: center;
  justify-self: center;
  background-color: white;
  padding: 10px;
  border-top-left-radius: 15px;
  border-top-right-radius: 15px;
}

#tab_btn_container > input {
  background: transparent;
  font-weight: bold;
  font-size: 1.5rem;
}

#login_tab_btn {
  border: none;
  margin-right: 5px;
}

#signup_tab_btn {
  border: none;
  margin-left: 5px;
}

#ls_content_container {
  padding: 40px;
}

.btn_active {
  color: gray;
}

.active {
  display: block !important;
}

#login_container {
  display: none;
  width: 100%;
  padding: 70px 30px;
  border-radius: 15px;
  background-color: #ffffff;
}

#login_container > form > div {
  padding: 15px;
}

#signup_container {
  padding: 70px 30px;
  display: none;
  border-radius: 15px;
  background-color: white;
}

#signup_container > form > div {
  padding: 10px;
}

.ls_label {
  font-size: 1rem;
  padding-left: 5px;
  font-weight: 700;
}

.ls_input_fields {
  color: black;
  padding: 10px;
  margin-top: 5px;
  border: 1px solid rgb(224, 222, 222);
  border-radius: 15px;
  width: 400px;
  font: bold;
  font-size: 0.8rem;
  font-family: "Segoe UI", Tahoma, Geneva, Verdana, sans-serif;
  background: transparent;
}

.form_feedback {
  font-size: 0.8rem;
  color: red;
  padding-top: 2px;
  padding-left: 10px;
  word-wrap: break-word;
}

.submit_btn_container {
  text-align: center !important;
}

.submit_btn {
  background-color: #299ac5;
  color: #e2f3f5;
  font-size: 1.2rem;
  font-weight: 600;
  border: none;
  border-radius: 5px;
  box-shadow: 0px 0px 4px #299ac5;
  padding: 5px 20px 5px 20px;
  transition: 0.5s ease;
}

.submit_btn:hover {
  background-color: white;
  color: #283158;
  box-shadow: 0px 0px 2px black;
}

/*END*/

/* Sponsor ship COmponent css*/

.sponsor_header {
  display: flex;
  background: url(/Parho-Pakistan-Front-End/static/media/sponsor_comp_header.fcd83e67.png);
  background-position: center center;
  background-size: cover;
  height: 500px;
  flex-direction: column;
  justify-content: flex-end;
}

.bread_crumbs {
  padding: 0px 30px;
}

.bread_crumbs > a,
.bread_crumbs span {
  color: #9d9d9d;
  text-decoration: none;
  font-size: 0.8rem;
}

.sponsor_header > h1 {
  padding: 0 30px;
  color: white;
  font-size: 3rem;
}

.sponsor_quote {
  height: auto;
  padding: 30px 50px 50px;
}

.sponsor_quote > p:nth-of-type(1) {
  font-family: Sitka Subheading;
  font-style: italic;
  font-weight: bold;
  font-size: 1.1rem;
  text-align: center;
  text-justify: auto;
}

.sponsor_quote > p:nth-of-type(2) {
  text-align: center;
  font-style: italic;
  font-size: 0.8rem;
}

.sponsor_filters {
  display: grid;
  grid-template-columns: 1fr 1fr 1fr;
  justify-items: center;
  padding: 50px 0 100px;
}

.sponsor_selectors {
  border: 1px solid rgb(194, 193, 193);
  width: 100px;
  color: grey;
}

.profiles_container {
  display: grid;
  grid-template-columns: auto auto auto;
  padding: 50px;
  justify-content: center;
}

.profile_card {
  width: 300px;
  border: 1px solid #ffffff;
  box-shadow: 0px 0px 5px #707070;
  margin: 10px;
  text-decoration: none;
}

.profile_card_img > img {
  width: 100%;
  padding-bottom: 20px;
}

.profile_card_content {
  display: grid;
  justify-content: center;
  grid-template-columns: auto auto;
  text-decoration: none;
}

.profile_card_content > span {
  color: grey;
  padding-right: 50px;
  font-weight: 600;
  text-decoration: none;
}

.profile_card_content > p {
  color: black;
  text-decoration: none;
  text-align: end;
}

.profile_card > p {
  text-align: center;
  padding: 20px 0;
  font-weight: bold;
  color: black;
}

