/* Child Profile CSS*/

.child_profile {
  background: url(/Parho-Pakistan-Front-End/static/media/childprofilebg.5c4d831f.svg);
  background-position: center center;
  background-size: cover;
  background-repeat: no-repeat;
  display: grid;
  grid-template-columns: 40% auto;
  min-height: 800px;
  padding: 100px 50px;
}

.profile_img > img {
  border: 8px solid white;
}

.profile_img,
.profile_img > img {
  width: 300px;
  height: 300px;
  border-radius: 100%;
  justify-self: end;
}

.profile_detail {
  display: grid;
  padding: 70px 0 0;
  grid-template-columns: auto auto;
}

.profile_detail span {
  justify-self: center;
  font-weight: bold;
  padding: 10px 0;
}

.profile_detail p {
  font-size: 1.4rem;
  padding: 10px 0;
}

.profile_content > h1 {
  text-align: center;
  padding: 20px 0;
}

.profile_vid {
  width: 100%;
  height: 500px;
  margin-top: 50px;
}

/*END*/

@media screen and (max-width: 1024) {
  #nav_container {
    justify-content: center;
  }
}

@media screen and (max-width: 768px) {
  .nav_container {
    display: none;
    z-index: 10;
    grid-template-columns: auto;
  }

  .nav_item {
    padding-left: 10px;
    padding-top: 15px;
    width: 100%;
  }

  #toggler_btn {
    display: block;
  }

  .dropdown_container {
    position: relative;
  }

  .dropdown_link:focus .dropdown_container {
    display: block;
  }

  #whyus {
    display: grid;
    grid-template-columns: auto;
    text-align: center;
  }

  .whyus_content {
    padding-top: 50px;
  }

  .homepage_details {
    grid-template-columns: auto;
  }
  .success_carousel {
    padding-bottom: 100px;
  }

  .sponsorships {
    grid-template-columns: auto;
  }
  .sponsorship_card {
    margin-bottom: 30px;
  }

  .profiles_container {
    grid-template-columns: auto auto;
  }

  .about_container {
    /* Permalink - use to edit and share this gradient: https://colorzilla.com/gradient-editor/#ffffff+36,ffffff+37,004197+37,4580fc+57 */
    background: #ffffff; /* Old browsers */ /* FF3.6-15 */ /* Chrome10-25,Safari5.1-6 */
    background: linear-gradient(
      to bottom,
      #ffffff 36%,
      #ffffff 37%,
      #004197 37%,
      #4580fc 57%
    ); /* W3C, IE10+, FF16+, Chrome26+, Opera12+, Safari7+ */
    filter: progid:DXImageTransform.Microsoft.gradient( startColorstr='#ffffff', endColorstr='#4580fc',GradientType=0 ); /* IE6-9 */
    grid-template-columns: auto;
    justify-items: center;
    text-align: center;
    padding: 100px 0;
  }

  .about_signup {
    /* Permalink - use to edit and share this gradient: https://colorzilla.com/gradient-editor/#4580fc+2,4580fc+67,004197+100 */
    background: #4580fc; /* Old browsers */ /* FF3.6-15 */ /* Chrome10-25,Safari5.1-6 */
    background: linear-gradient(
      to bottom,
      #4580fc 2%,
      #4580fc 67%,
      #004197 100%
    ); /* W3C, IE10+, FF16+, Chrome26+, Opera12+, Safari7+ */
    filter: progid:DXImageTransform.Microsoft.gradient( startColorstr='#4580fc', endColorstr='#004197',GradientType=0 ); /* IE6-9 */
    justify-content: center;
    padding: 0px 0 100px;
    text-justify: auto;
  }

  .about_container > img {
    width: 60%;
    margin-bottom: 10px;
  }

  .signup_container {
    width: 100%;
    padding: 0;
  }

  .child_profile {
    /* Permalink - use to edit and share this gradient: https://colorzilla.com/gradient-editor/#fd882a+8,ff9933+27,ff9933+27,ffffff+27,ffffff+27 */
    background: #fd882a; /* Old browsers */ /* FF3.6-15 */ /* Chrome10-25,Safari5.1-6 */
    background: linear-gradient(
      to bottom,
      #fd882a 8%,
      #ff9933 27%,
      #ff9933 27%,
      #ffffff 27%,
      #ffffff 27%
    ); /* W3C, IE10+, FF16+, Chrome26+, Opera12+, Safari7+ */
    filter: progid:DXImageTransform.Microsoft.gradient( startColorstr='#fd882a', endColorstr='#ffffff',GradientType=0 ); /* IE6-9 */
    grid-template-columns: auto;
  }

  .profile_img {
    justify-self: center;
  }
}

@media screen and (max-width: 700px) {
  .success_carousel {
    background-position: center center;
    background-size: 100%;
  }

  .success_header {
    font-size: 4vw;
  }

  .carousel_content img {
    width: 30vw;
    height: 30vw;
  }

  #sponsorship_sec h1 {
    font-size: 5vw;
    justify-self: center;
    padding: 0;
  }

  .collab_img_container {
    grid-template-columns: 1fr 1fr;
  }

  footer {
    grid-template-columns: 1fr;
    justify-items: center;
  }

  footer h1 {
    font-size: 8vw;
    justify-self: center;
  }

  .website_links {
    padding: 20px 0;
    justify-self: center;
  }

  #ls_container {
    padding: 0px;
    background-color: transparent;
    border: none;
  }

  #ls_content_container {
    padding: 0px;
  }

  #login_container {
    padding: 70px 30px;
  }
  #signup_container {
    padding: 70px 30px;
  }
  .ls_input_fields {
    width: 250px;
  }

  .profiles_container {
    grid-template-columns: auto;
  }

  .about_container > img {
    width: 320px;
    padding-top: 50px;
  }

  .signup_container {
    font-size: 0.8rem;
  }

  .profile_img {
    padding-top: 70px;
  }
  .profile_img,
  .profile_img > img {
    width: 250px;
    height: 250px;
  }
}

